body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Header {
    display: flex;
}

Table {
    width: 100%;
}

.message {
    margin-top: 20px;
}

span {
    color: #00AB6B;;
}

Table th, tr {
    text-align: center;
}



.header {
    display: flex;
}

.form {
    margin: 15px 25px; 
}

.form input {
    margin-right: 10px;
    width: 300px;
}

.submit {
    background-color: #00AB6B;
    border: none;
    border-radius: 2px;
    color: #fff;
    padding: 5px 35px;
    outline: 0;
}

.reset {
    background-color: #fff;
    border: none;
    color: #00AB6B;
    height: 25px;
    padding: 0;
    margin-top: 18px;
}

.reset:hover {
    border-bottom: 1px solid #00AB6B
}



.deleteButton {
    background: none;
    border: none;
}

.deleteButton:hover {
    color: #00AB6B;
}
.addUserBtn button {
    width: 150px;
    background-color: #fff;
    border: 1px solid #00AB6B;
    border-radius: 2px;
    color: #00AB6B;
    padding: 5px 25px;
    margin: 15px 25px;
    outline: 0;
}

.addUserBtn button:hover {
    background-color: #00AB6B; 
    color: #fff;
}

.addUserBtn button:focus {
    outline: none;
}

.addUserBtn button span {
    font-weight: 600;
    margin-left: 5px;
}

